export function getBanner(){
  return new Promise(async (resolve,reject)=>{
    try{
      axios(getPostOption("lobby.banner",{})).then( (response)=>{
        const data = handlData(response.data);
        resolve( data )
      } )
    }catch (err){
      reject(err);
    }
  });
}

