function getPostOption( method, data ){
  return {
    method: 'post',
    url: '/api',//'http://127.0.0.1:6403/api',//
    data: {
      method:method,
      data:data
    }
  }
}

function handlData( data ){
  if( data.code === 1 ){
    return data.data;
  }else{
    throw new Error(data.msg);
  }
}







